<template>
  <!--  -->
  <section class="loginContainer">
    <div class="loginInner">
      <div class="login_header">
        <h2 class="login_logo"></h2>
        <div class="login_header_title">
          <a href="javascript:;" :class="{on: loginWay}" @click="loginWay=true"></a>
          <a href="javascript:;" :class="{on: !loginWay}" @click="loginWay=false"></a>
        </div>
      </div>
      <div class="login_content">
        <!--   -->
        <form @submit.prevent="login">
          <div :class="{on: loginWay}">
            <section class="login_message">
              <input type="tel" maxlength="11" placeholder="" v-model="phone" />
              <!-- disabled  prevent -->
              <button
                :disabled="!rightPhone"
                class="get_verification"
                :class="{right_phone: rightPhone}"
                @click.prevent="getCode"
              >{{computeTime>0 ? `${computeTime}s` : ''}}</button>
            </section>
            <section class="login_verification">
              <input type="tel" maxlength="6" placeholder="" v-model="code"/>
            </section>
            <section class="login_hint">
              
              <a href="javascript:;"></a>
            </section>
          </div>
          <div :class="{on: !loginWay}">
            <section>
              <section class="login_message">
                <input type="text" maxlength="11" placeholder="//" v-model="name"/>
              </section>
              <section class="login_verification">
                <input type="text" maxlength="8" placeholder="" v-if="showPwd" v-model="pwd" />
                <input type="password" maxlength="8" placeholder="" v-else v-model="pwd" />
                <!--  -->
                <div class="switch_button off" :class="showPwd?'on':'off'" @click="showPwd=!showPwd">
                  <div class="switch_circle" :class="{right: showPwd}"></div>
                  <span class="switch_text">{{showPwd?'':''}}</span>
                </div>
              </section>
              <section class="login_message">
                <input type="text" maxlength="11" placeholder="" v-model="captcha" />
                <img class="get_verification" src="http://guigu_zhipinjiekou.gjxbewater.cn:8080/captcha" alt="captcha"
                  @click="getCaptcha" ref="captcha"
                />
              </section>
            </section>
          </div>
          <button class="login_submit"></button>
        </form>
        <a href="javascript:;" class="about_us"></a>
      </div>
      <a href="javascript:" class="go_back" @click="$router.replace('/msite')">
        <i class="iconfont icon-jiantou-copy"></i>
      </a>
    </div>
    <!--  -->
    <AlertTip :alertText="alertText" v-show="alertShow" @closeTip="closeTip"/>
  </section>
</template>

<script>
import AlertTip from '../../components/AlertTip/AlertTip.vue'
// ajax     
import {reqSendCode, reqSmsLogin, reqPwdLogin} from '../../api'

export default {
  data () {
    return {
      loginWay: true, // truefalse
      phone: '', // 
      computeTime: 0, // 
      showPwd: false, // 
      name: '',
      pwd: '', // 
      code: '', // 
      captcha: '', // 
      alertText: '', // 
      alertShow: false // 

    }
  },
  // 
  methods: {
    // 
    async getCode () {
      // 
      if (!this.computeTime) {
        // 
        // alert("------");
        this.computeTime = 30
        // 
        this.IntervalId = setInterval(() => {
          this.computeTime--
          if (this.computeTime <= 0) {
            // 
            clearInterval(this.IntervalId)
          }
        }, 1000)

        // ajax  
        const result = await reqSendCode(this.phone)
        // 
        if (result.code === 1) {
          // 
          this.showAlert(result.msg)
          // 
          if (this.computeTime) {
            this.computeTime = 0
            clearInterval(this.IntervalId)
            this.IntervalId = undefined
          }
        }
      }
    },
    //   
    showAlert (alertText) {
      this.alertShow = true
      this.alertText = alertText
    },
    // 
    async login () {
      let result
      // 
      if (this.loginWay) { // 
        const {phone, code} = this
        if (!this.rightPhone) {
          // 
          this.showAlert('')
          return
        } else if (!/^\d{6}$/.test(code)) {
          // 6
          this.showAlert('6')
          return
        }
        // ajax
        result = await reqSmsLogin(phone, code)
      } else { // 
        const {name, pwd, captcha} = this
        if (!this.name) {
          // 
          this.showAlert('')
          return
        } else if (!this.pwd) {
          // 
          this.showAlert('')
          return
        } else if (!this.captcha) {
          // 
          this.showAlert('')
          return
        }
        // ajax
        result = await reqPwdLogin({name, pwd, captcha})
      }
      // 
      if (this.computeTime) {
        this.computeTime = 0
        clearInterval(this.IntervalId)
        this.IntervalId = undefined
      }
      // result  
      if (result.code === 1) {
        result = {
          data: {
            _id: 1000,
            name: this.name,
            phone: this.phone
          }
        }
        const user = result.data
        // userveuxstate
        this.$store.dispatch('recordUser', user)
        // 
        this.$router.replace('/profile')
      } else {
        //   
        this.getCaptcha()
        this.captcha = ''
        // 
        const msg = result.msg
        this.showAlert(msg)
      }
    },
    // 
    closeTip () {
      this.alertShow = false
      this.alertText = ''
    },
    // 
    getCaptcha () {
      // imgsrc
      // 
      this.$refs.captcha.src = 'http://guigu_zhipinjiekou.gjxbewater.cn:8080/captcha?time=' + Date.now()
    }
  },
  // 
  computed: {
    // phone
    rightPhone () {
      // 110   
      return /^1\d{10}$/.test(this.phone)
    }
  },
  components: {
    AlertTip
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import '../../common/stylus/mixins.styl';

.loginContainer {
  width: 100%;
  height: 100%;
  background: #fff;

  .loginInner {
    padding-top: 60px;
    width: 80%;
    margin: 0 auto;

    .login_header {
      .login_logo {
        font-size: 40px;
        font-weight: bold;
        color: #02a774;
        text-align: center;
      }

      .login_header_title {
        padding-top: 40px;
        text-align: center;

        >a {
          color: #333;
          font-size: 14px;
          padding-bottom: 4px;

          &:first-child {
            margin-right: 40px;
          }

          &.on {
            color: #02a774;
            font-weight: 700;
            border-bottom: 2px solid #02a774;
          }
        }
      }
    }

    .login_content {
      >form {
        >div {
          display: none;

          &.on {
            display: block;
          }

          input {
            width: 100%;
            height: 100%;
            padding-left: 10px;
            box-sizing: border-box;
            border: 1px solid #ddd;
            border-radius: 4px;
            outline: 0;
            font: 400 14px Arial;

            &:focus {
              border: 1px solid #02a774;
            }
          }

          .login_message {
            position: relative;
            margin-top: 16px;
            height: 48px;
            font-size: 14px;
            background: #fff;

            .get_verification {
              position: absolute;
              top: 50%;
              right: 10px;
              transform: translateY(-50%);
              border: 0;
              color: #ccc;
              font-size: 14px;
              background: transparent;

              &.right_phone {
                color: black;
              }
            }
          }

          .login_verification {
            position: relative;
            margin-top: 16px;
            height: 48px;
            font-size: 14px;
            background: #fff;

            .switch_button {
              font-size: 12px;
              border: 1px solid #ddd;
              border-radius: 8px;
              transition: background-color 0.3s, border-color 0.3s;
              padding: 0 6px;
              width: 30px;
              height: 16px;
              line-height: 16px;
              color: #fff;
              position: absolute;
              top: 50%;
              right: 10px;
              transform: translateY(-50%);

              &.off {
                background: #fff;

                .switch_text {
                  float: right;
                  color: #ddd;
                }
              }

              &.on {
                background: #02a774;
              }

              >.switch_circle {
                // transform translateX(27px)
                position: absolute;
                top: -1px;
                left: -1px;
                width: 16px;
                height: 16px;
                border: 1px solid #ddd;
                border-radius: 50%;
                background: #fff;
                box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
                transition: transform 0.3s;
                &.right{
                  transform: translateX(30px)
                }
              }
            }
          }

          .login_hint {
            margin-top: 12px;
            color: #999;
            font-size: 14px;
            line-height: 20px;

            >a {
              color: #02a774;
            }
          }
        }

        .login_submit {
          display: block;
          width: 100%;
          height: 42px;
          margin-top: 30px;
          border-radius: 4px;
          background: #4cd96f;
          color: #fff;
          text-align: center;
          font-size: 16px;
          line-height: 42px;
          border: 0;
        }
      }

      .about_us {
        display: block;
        font-size: 12px;
        margin-top: 20px;
        text-align: center;
        color: #999;
      }
    }

    .go_back {
      position: absolute;
      top: 5px;
      left: 5px;
      width: 30px;
      height: 30px;

      >.iconfont {
        font-size: 20px;
        color: #999;
      }
    }
  }
}
</style>



